import React from 'react';
import { Link } from 'react-router-dom';
import '../style/main.css'; 
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMagnifyingGlass } from "@fortawesome/free-solid-svg-icons";
function Blog(props) {
  return (
    <section className='blog'>
      <div>
        <h2></h2>

        <div className='search'>
          <label><FontAwesomeIcon icon={faMagnifyingGlass}/></label>
          <input type='search' placeholder=' .'/>
        </div>
      </div>

      <div className='blog_img'>
        <div>
          <Link to='https://allra-front-assignment.vercel.app/blogs/11'>
            <img src={`${process.env.PUBLIC_URL}/images/banner1.png`} alt=' ' />
          </Link>
        </div>
        <div>
          <Link to='https://allra-front-assignment.vercel.app/blogs/230'>
            <img src={`${process.env.PUBLIC_URL}/images/banner2.png`} alt='  3' />
          </Link>
        </div>
      </div>
    </section>
  );
}

export default Blog;